export interface Agreement {
  id: number
  name: string
  uri: string
  required_for: string
  added_at: number
  signed_at: null | number
  signatory_name: null | string
}

export interface CopyrightClaim {
  id: number
  added_at: number
  status: string
  status_translated: string
  url: string
  last_status_validation_issue: string
}

export enum LegalEntityType {
  Person = "Physical person",
  Company = "Company",
  NonProfit = "Non-profit",
  Institution = "Institution",
}

export enum LegalEntityStatus {
  Pending = "pending",
  Rejected = "rejected",
  Ok = "ok",
}

export interface LegalEntity {
  type: LegalEntityType
  account_id: number
  id: number
  name: string
  address: string
  country: string
  vat_number: string
  registry_code: string
  bank_name: string
  bank_swift: string
  bank_address: string
  bank_iban: string
  representative_name: string
  representative_number: string
  representative_email: string
  representative_position: string
  signatory_name: string
  signatory_number: string
  signatory_email: string
  signatory_position: string
  status: LegalEntityStatus
  status_translated: string
  last_status_validation_issue: string
  added_at: number
}

export enum LegalFileStatus {
  Pending = "pending",
  Ok = "ok",
  Rejected = "rejected",
}

export interface LegalFile {
  id: number
  name: string
  status: LegalFileStatus
  status_translated: string
  uri: string
  sha1_hash: string
  legal_entity_id: number
  added_at: number
  last_status_validation_issue: string
}
